import Vue from 'vue';
import asyncComputed from 'vue-async-computed';
import VueAnalytics from 'vue-analytics';
import vueCustomElement from 'vue-custom-element';
import App from './App.vue';
import store from './store/index';
// import mixins from './mixins/index';

// carousel

import './assets/css/main.scss';
// widget setup
// (optional) 'Custom elements polyfill'
import 'document-register-element/build/document-register-element';
// vue-custom-element by karol-f

Vue.use(vueCustomElement);

// async computed
Vue.use(asyncComputed);

Vue.config.productionTip = false;

// new Vue is not needed anymore, as you're initializing the app through vue-custom-element
// new Vue({
//   router,
//   store,
//   render: h => h(App)
// }).$mount('#app')

const isLocal = window.location.host === 'localhost:8080';
// const isTest = true
// use vue-custom-element
Vue.use(VueAnalytics, {
  id: 'UA-118326062-1',
  autoTracking: {
    exception: true,
  },
  debug: {
    enabled: !isLocal,
    sendHitTask: !isLocal,
  },
  ecommerce: {
    enabled: true,
    enhanced: true,
  },
  cookieDomain: isLocal ? 'none' : 'yesti.me',
});
App.store = store;
// Vue.mixin(mixins);
Vue.customElement('yestime-offer', App);
