<template>
  <div>
    <Spinner
      v-if="loading"
      :style="{ 'top': this.widgetHeight/2 + 'px',
      'margin-top': loading ? -40 + 'px' : '' }"
    ></Spinner>

    <Carousel
        :adjustableHeight="true"
        :autoplay="true"
        :autoplayTimeout="3000"
        :class="{ 'branding-disabled': !showBranding }"
        :loop="true"
        :paginationEnabled="false"
        :per-page="1"
        :spacePadding="0"
        :speed="1000"
        @pageChange="handleAdImpressions"
        class="y-carousel y-responsive"
        ref="carousel"
    >
      <Slide
          v-for="(t, index) in tagList"
          :data-index="index"
          :key="index"
      >
        <a
            @click=handleAdClick(index)
            class="y-carousel-link"
            data-index="index"
            target="_blank"
        >
          <img
            v-if="loadingImg(index)"
            :src="t.imageUrl"
            :height="widgetHeight"
            :width="widgetWidth"
            alt=""
            id="tag-image"
          >
          <Spinner v-else></Spinner>
        </a>
      </Slide>
    </Carousel>
    <div v-if="isShowBrandLogo"
      class="brand-logo"
      :style="{ top: widgetHeight - 15 + 'px' }"
    >
      <span>Powered By <a href="https://yestime.com" >
        <img class="logo-img" src="https://yestime.com/wp-content/uploads/2018/12/logo.png" alt="YesTime"></a>
      </span>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import { Carousel, Slide } from 'vue-carousel';
import Spinner from './Spinner.vue';
import noImageAvailable from '@/assets/img/no-image.png';

export default {
  name: 'offer',
  components: {
    Carousel,
    Slide,
    Spinner,
  },
  props: {
    token: {
      default: '',
    },
    type: {
      default: '',
    },
    width: {
      default: 350,
    },
    height: {
      default: 350,
    },
    format: {
      default: 'jpeg',
    },
    rank: {
      default: 1,
    },
    eventId: {
      default: '',
    },
    showBranding: {
      default: false,
    },
  },
  data() {
    return {
      adminApiEndpoint: `${process.env.VUE_APP_ADMIN_API_ENDPOINT}/api/v1`,
      analyticsApiUrl: '',
      htmlApiUrl: '',
      types: ['CJ-MANUAL', 'WAY-PARKING'],
      attemptCount: 0,
      count: 0,
      errorCode: null,
      errorRequestLimit: 10,
      errorStatus: '',
      eventData: {
        eventCategory: '',
        eventAction: '',
        eventLabel: '',
        eventValue: null,
      },
      interval: {},
      isShowBrandLogo: this.showBranding,
      leftTime: 10,
      loading: false,
      tagList: [],
      widgetHeight: 0,
      widgetRatio: 0,
      widgetWidth: 0,
    };
  },
  mounted() {
    this.handleResize()
      .then(() => {})
      .catch(() => {});
    this.loading = true;
    const offerType = String(this.type).toLowerCase();

    if (this.type) {
      this.htmlApiUrl = `${this.adminApiEndpoint}/html/${this.type}/${this.rank}`;
      this.analyticsApiUrl = `${this.adminApiEndpoint}/analytics`;
    }
    if (offerType === 'way-parking') {
      this.htmlApiUrl = `${this.htmlApiUrl}?width=${this.width}&height=${this.height}&eventId=${this.eventId}&format=${this.format}`;
    } else if (offerType === 'cj-manual') {
      this.htmlApiUrl = `${this.htmlApiUrl}?creativewidth=${this.width}&creativeheight=${this.height}&format=${this.format}`;
    }
    this.getHtml(this.htmlApiUrl);

    this.$nextTick(() => {
      window.addEventListener('resize', this.handleResize);
    });
    this.assignClass('VueCarousel-wrapper', 'y-responsive__sizer');
    this.assignClass('VueCarousel-inner', 'y-responsive__content');

    this.changeRatio(this.height, this.width);
    this.handleResize()
      .then(() => {})
      .catch(() => {});

    this.addPage(window.location.href, 'Yestime Widget');
    this.eventData = {
      eventCategory: 'WIDGET/YESTIME',
      eventAction: 'LOAD',
      eventLabel: window.location.href,
      eventValue: null,
    };
    this.addEvent(this.eventData);
    this.analyticsSend('WIDGET/YESTIME LOAD');
    this.eventData = {};
  },
  beforeDestroy() {
    clearInterval(this.interval);
  },
  methods: {
    addEvent(data) {
      this.analytics().event(data);
    },

    analytics() {
      // console.log(this.$ga)
      return this.$ga;
    },

    // send offer action and action history to admin back-end
    async analyticsCreate(offerId, offerAction, count) {
      axios.defaults.headers.common.Authorization = `Bearer ${this.token}`;

      const actionData = {
        offerId,
        actions: {},
        actionHistory: [{
          occurred: new Date(),
          count,
          action: offerAction,
        }],
      };
      actionData.actions[offerAction] = count;

      await axios
        .post(
          `${this.analyticsApiUrl}/create`, actionData,
        )
        .then(res => res)
        .catch(err => err);
    },

    analyticsSend(eventInfo) {
      this.analytics().query('send', eventInfo);
    },

    addPage(page, title) {
      this.analytics().page({
        page,
        title,
        location: window.location.href,
      });
    },

    addProduct(data) {
      this.analytics().ecommerce.addProduct(data);
    },

    addProductClick(data) {
      this.analytics().ecommerce.setAction('click', data);
      // this.$ga.ecommerce.addItem(data)
    },

    addProductImpression(data) {
      this.analytics().ecommerce.addImpression(data);
    },

    /**
     * assignClass() add new dom class
     * @param target
     * @param assign
     */
    assignClass(target, assign) {
      const cls = document.getElementsByClassName(target);
      for (let i = 0; i < cls.length; i += 1) {
        cls[i].className = `${cls[i].className} ${assign}`;
      }
    },

    /**
     * changeRatio() control the widget padding-bottom css
     * @param h
     * @param w
     */
    changeRatio(h, w) {
      const dimension = Number(h) / Number(w) * 100;
      this.widgetRatio = dimension.toFixed(2);
      const cls = document.getElementsByClassName('y-responsive__sizer');
      for (let i = 0; i < cls.length; i += 1) {
        cls[i].style['padding-bottom'] = `${this.widgetRatio}%`;
      }

      return this.widgetRatio;
    },

    /**
     * getHtml()
     * @param url
     */
    async getHtml(url) {
      await axios
        .get(
          url,
          {
            headers: { token: `Bearer ${this.token}` },
          },
        )
        .then((res) => {
          const { data } = res;
          if (!data.hasErrors) {
            this.tagList = data.recommendation.offer;
            this.loading = false;
            this.tagList.forEach((t) => {
              t.adInfo = {
                id: t.offerId,
                adName: t.adName,
                category: t.category,
                brand: t.brand,
                variant: '',
                price: null,
                quantity: 1,
              };
            });
          } else {
            this.errorStatus = res.data.errors.status;
            this.errorCode = res.data.errors.code;
            this.errorRedirecting();
          }
        })
        .catch(() => {
          this.errorStatus = 'HTTP_NOT_FOUND';
          this.errorCode = 404;
          this.errorRedirecting();
        });
    },

    /**
     * errorRedirecting() sets the tag's initial values when got the server error response
     */
    errorRedirecting() {
      const tag = {
        redirectUrl: '',
        imageUrl: noImageAvailable,
        adInfo: {
          id: this.type,
          category: `ERROR-REDIRECT/${this.errorCode}`,
          adName: '',
          brand: '',
          variant: '',
          price: null,
          quantity: 1,
        },
      };

      if (this.tagList && this.tagList.length > 0) {
        this.tagList.splice(this.count, 1, tag);
      }

      this.eventData = {
        eventCategory: `ERROR-REDIRECT/${this.errorCode}`,
        eventAction: 'IMPRESSION',
        eventLabel: '',
        eventValue: null,
      };
      const adId = `${this.type}-${this.eventId}-0`;
      const adName = `${this.type} - tn event # ${this.eventId}`;

      this.addEvent(this.eventData);

      this.analyticsSend(`Category Impression: ${adName}, id: ${adId}`);
      this.loading = false;
    },

    /**
     * handleAdClick() generates the impression and redirect to tag.link
     * @param rank
     */
    handleAdClick(rank) {
      const curTag = this.tagList[rank];
      this.eventData.eventCategory = curTag.adInfo.category;
      this.eventData.eventAction = 'CLICK';
      this.eventData.eventLabel = curTag.adInfo.id;
      this.eventData.eventValue = null;

      this.addEvent(this.eventData);

      this.analyticsSend(`Category Click: ${curTag.adInfo.name}, id: ${curTag.adInfo.id}`);

      this.analyticsCreate(curTag.adInfo.id, this.eventData.eventAction, 1);
      window.location.href = curTag.redirectUrl;
    },

    /**
     * handleAdImpressions() generate the ad impression with carousel moving
     * @param currentPage
     */
    handleAdImpressions(currentPage) {
      const curTag = this.tagList[currentPage];
      this.eventData.eventCategory = curTag.adInfo.category;
      this.eventData.eventAction = 'IMPRESSION';
      this.eventData.eventLabel = curTag.adInfo.id;
      this.eventData.eventValue = null;

      this.addEvent(this.eventData);

      this.analyticsSend(`Category Impression: ${curTag.adInfo.adName}, id: ${curTag.adInfo.id}`);

      this.analyticsCreate(curTag.adInfo.id, this.eventData.eventAction, 1);
    },

    /**
     * handleResize() resets the widget's width and height
     */
    handleResize() {
      return new Promise((resolve, reject) => {
        const domElement = document.getElementsByClassName('y-carousel');
        // this.widgetWidth = this.$refs.carousel.carouselWidth;

        if (domElement) {
          this.widgetWidth = domElement[0].clientWidth;
          this.widgetHeight = domElement[0].clientHeight;
          if (this.showBranding) {
            if (this.widgetWidth < 450) {
              this.isShowBrandLogo = false;
              domElement[0].style['padding-bottom'] = 0;
            } else {
              this.isShowBrandLogo = true;
              domElement[0].style['padding-bottom'] = `${29}px`;
            }
          }
          resolve(true);
        } else {
          reject(new Error('DOM tracking failed'));
        }
      });
      // this.height = this.widgetHeight
    },

    /**
     * loadingImg() returns the status of each image loading on carousel
     * @param index
     * @returns {boolean}
     */
    async loadingImg(index) {
      return await this.tagList[index].imageUrl !== '';
    },

    /**
     * timeCycle() is a timer of 10s counting
     */
    timeCycle() {
      this.interval = setInterval(() => {
        if (this.leftTime <= 0) {
          this.leftTime = 10;
        }
        this.leftTime -= 1;
      }, 1000);
    },
  },
  watch: {
    errorCode(val) {
      if (val > 0) {
        this.timeCycle();
      }

      return val;
    },
  },
  asyncComputed: {
    /**
     * dynamicTag() tracks the events of 5xx server issues and controls the 10s timer
     */
    dynamicTag() {
      if (this.leftTime === 0 && this.attemptCount < this.errorRequestLimit
        && this.errorCode >= 500) {
        this.getHtml(this.adminApiEndpoint);
        this.attemptCount += 1;
        if (this.attemptCount >= 10) {
          clearInterval(this.interval);
        }
      }

      return this.attemptCount;
    },
  },
};
</script>
