import Vue from 'vue';
import Vuex from 'vuex';
import { analyticsMiddleware } from 'vue-analytics';

// import moment from 'moment'

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    counter: 0,
  },
  getters: {
    counter: state => state.counter,
  },
  actions: {

  },
  mutations: {
    increase(state, { amount }) {
      state.counter = amount;
    },
  },
  plugins: [
    analyticsMiddleware,
  ],
});
